.sidebar{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transition: all 0.3s ease-out;
    z-index: 20;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding: 20px 0;
    .title{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin-bottom: 2rem;
        a{
                    text-decoration: none;

        }

        .logo{
            color:rgb(8, 82, 109);
            font-size: 3rem;
            font-family: 'Roboto Condensed', sans-serif;
            font-weight: 500;
            letter-spacing: -1;
        
            &:hover{
                cursor: pointer;
            }
        }
        .logoSpan{
            color:#E14618;
            font-family: 'Roboto', sans-serif;
            font-weight: 300;
            font-size: 3rem;
        }
        .close{
            width: 20%;
            cursor: pointer;
            svg{
                width: 100%;
                &:hover{
                    g{
                        path{
                            fill: #d5293f;
                        }
                    }
                }
            }
        }
    }
    .Links{
        z-index: 20;
        margin: 3 0;
        padding: 0;
        list-style-type: none;
        li,a{
            margin: 1rem 1.5rem;
            cursor: pointer;
            font-weight: 500;
            font-size: 1.2rem;
            text-decoration: none;
            color: black;
            text-align: center;
            margin-bottom: 2rem;
        }

    }
}

.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);
}